import logo from "./logo_snacks_complete.png";
import "./App.css";
import NavBar from "./components/NavBar/NavBar";

import SnacksContainer from "./components/SnacksContainer/SnacksContainer";
import { useState } from "react";
import ProfileCard from "./components/ProfileCard/ProfileCard";
import AboutUs from "./components/AboutUs/AboutUs";

function App() {
  const [show, setShow] = useState(false);
  const [showA, setShowA] = useState(false);
  const [contactExpert, setContactExpert] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const handleCloseA = () => setShowA(false);
  const handleShowA = () => setShowA(true);
  return (
    <div
      className="App text-center"
      style={{
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-between",
        minHeight: "100vh",
      }}
    >
      <NavBar
        setCE={() => setContactExpert(!contactExpert)}
        handleShow={handleShow}
        handleShowA={handleShowA}
      />
      <div
        className="px-5 "
        style={{
          position: "relative",
        }}
      >
        <SnacksContainer />
        <AboutUs
          show={showA}
          handleClose={handleCloseA}
          handleShow={handleShowA}
        />
        <ProfileCard
          show={show}
          handleClose={handleClose}
          handleShow={handleShow}
        />
      </div>
      <div
        className="text-center text-white py-2"
        style={{ backgroundColor: "rgb(23 26 29)" }}
      >
        <p className="mb-0" style={{ display: "inline", color: "gray" }}>
          Snacks - All rights reserved{" "}
          <span className="text-light" style={{ display: "inline" }}>
            {" "}
            | Developed by AB
          </span>
        </p>
      </div>
    </div>
  );
}

export default App;
